}

.photosWrapper {
  width: 180px;
  height: 57px;
  margin: 0 auto 24px;
  padding: 0 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  overflow: hidden;

  // border: 1px solid red;

  @media screen and (min-width: $min_tablet) {
    width: 400px;
  }

  @media screen and (min-width: $min_desktop) {
    width: 515px;
  }

  @media screen and (min-width: $min_desktop_large) {
    // margin-bottom: 32px;
    // height: 80px;
    width: 950px;
  }
}

