.photoSmall {
  width: 37px;
  cursor: pointer;
  border-radius: 50%;
  opacity: 0.6;
  // transition-property: all;
  // transition-duration: 1s;
  // transition-timing-function: ease-in-out;
  // transition-delay: 0.5s;
  transition: all 0.5s ease-in-out 0.5s;

  // @media screen and (min-width: $min_tablet) {
  // }

  // @media screen and (min-width: $min_desktop_large) {
  //   width: 48px;
  // }
}

.photoSmall:hover {
  transform: scale(1.4);
}

.activePhotoSmall {
  transform: scale(1.4);
  opacity: 1;
  transition: all 0.5s ease-in-out 0.5s;
}

