}

.dot {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: var(--slider_dot_color);
  cursor: pointer;
  transition-duration: 0.5s;

  &:hover {
    // min-width: 8px;
    // min-height: 8px;
    transform: scale(1.6);
  }

  // @media screen and (min-width: $min_tablet) {
  // }

  // @media screen and (min-width: $min_desktop_large) {
  //   width: 10px;
  //   height: 10px;

  //   &:hover {
  //     width: 14px;
  //     height: 14px;
  //   }
  // }
}

.activeDot {
  // width: 8px;
  // height: 8px;
  border-radius: 50%;
  background-color: var(--active_slider_dot_color);
  cursor: pointer;
  transform: scale(1.6);
  transition-duration: 0.5s;

